          _createElementVNode("div", _hoisted_13, [
            _createElementVNode("p", _hoisted_14, " Я давно хотела черкнуть пару строк о влиянии физического развития на умственное – может кому и будут интересны мои рассуждения и выводы. Собрав декретные мысли в кулак и вооружившись интернетом, написалось.... С самых пеленок, большинство из мам задаются целью вырастить интеллектуально развитого ребенка. Скупаются магазины игрушек, книжные магазины, дома появляется огромные коробки Школы Семи гномов и Умницы, в развивающие центры чуть ли не с рождения…. И список всего, что делают большинство мам для развития умственных способностей ребенка, можно продолжать еще долго. Но с этого ли нужно начать умственное воспитание? Я считаю НЕТ. Это, конечно, всё должно присутствовать в жизни ребенка, но на первом месте – физическое воспитание. И уже оно потянет за собой умственное. В процессе физического воспитания оказывается прямое воздействие на развитие умственных способностей ребенка. Когда малыш ползет, бежит, катится на беговеле или ползет по лестнице возникают познавательные ситуации, связанные с овладением техникой физических упражнений, ее совершенствованием, овладением приемами практических действий и пр. (как выполнить движения экономнее, точнее, выразительнее и т.д.,). В результате занятий физическими упражнениями улучшается мозговое кровообращение, активизируются психические процессы, обеспечивающие восприятие, переработку и воспроизведение информации. Так, имеются многочисленные данные о том, что под влиянием физических упражнений увеличивается объем памяти, повышается устойчивость внимания, ускоряется решение элементарных интеллектуальных задач, убыстряются зрительно-двигательные реакции. Условием эффективной интеллектуальной деятельности является как и тренированный мозг, так и тренированное тело, мускулатуру, помогающую нервной системе справиться с умственными нагрузками. Активность и устойчивость внимания, памяти, переработки информации восприятия, напрямую зависит от уровня физической подготовки организма. Протекание психических процессов (память, внимание, мышление) сильно зависит от физических качеств организма — выносливости, быстроты, силы и др. Существует закономерность: чем больше у ребенка развивается способность управления своим телом, тем быстрее и лучше он усваивает теоретические знания, но чем симметричнее, разнообразнее и точнее движения, тем равномернее развиваются оба полушария головного мозга (левое полушарие отвечает за логику, правое – творчество, синхронная работа полушарий мозга означает большую успешность: мыслительный и познавательный процессы идут более легко и результативно). Первое, что заставляет полушария работать оба полушария в тандеме – ползание. Поэтому ползать – полезнее чем ходить!! И только лыжи могут так же заставить развиваться мозг!! А потом малыш начинает ходить) и происходит глобальный скачок в умственном развитии. И тут главное – не мешать ему развиваться: убрать навсегда коляску, велосипед с ручкой, всевозможные виды транспорта, упрощающие жизнь маме, но не дающие полноценно развиваться ребенку. Умственное и физическое воспитание очень тесно связаны: с одной стороны, физическое воспитание создает благоприятные условия для умственной деятельности и способствует развитию интеллекта. С другой стороны, умственное воспитание создает условия более вдумчивого отношения к физической активности, что существенно повышает эффективность физического воспитания. В более позднем возрасте, наверное лет с 6-7, умственные занятия будут выходить на первый план, а когда малышу год, два, – научится самому спускаться и подниматься по лестнице гораздо полезнее, чем в красную баночку положить красный кружок! ", 512),
